@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu', sans-serif;
  scroll-behavior: smooth;
  scroll-margin-top: 20px;
}
.blog {
    display: flex;
    align-items: center;
    padding: 0% 10% 0% 10%;
}

.blog-group {
    display: flex;
    flex-direction: column;
    margin-right: 15%;
}

.blog img {
    max-width: 500px;
}

.blog-group .head {
    font-size: 60px;
    line-height: 70px;
    letter-spacing: -1.87579px;
    color: #19191B;
}

.blog-group .desc {
    font-size: 20px;
    line-height: 30px;
    letter-spacing: -0.625263px;
    color: #696871;
    margin: 5% 0% 5% 0%;
}

.blog-group button {
    width: 50%;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: -0.500211px;
    padding: 10px 40px;
    color: #FFFFFF;
    background: #5454D4;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5%;
    height: 50px;
    background-color: #5454D4;
    font-weight: bold;
    color: #FFFFFF;
}

@media screen and (max-width: 1080px) {
    .blog {
        flex-direction: column;
    }

    .blog-group {
        width: 80%;
    }
    
    .blog-group .head {
        font-size: 30px;
        line-height: normal;
    }

    .blog-group .desc {
        font-size: 16px;
        line-height: normal;
        margin-bottom: 20%;
    }

    .blog-group button {
        width: 90%;
        margin-bottom: 10%;
    }

    .blog img {
        width: 90%;
    }
}
.feature-section {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
}

.feature1-group {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 10% 15% 0% 5%;
}

.feature1 {
    display: flex;
    justify-content: space-evenly;
}

.feature1-group .head {
    font-size: 50px;
    line-height: 57px;
    letter-spacing: -1.56316px;
}

.feature1-group .desc {
    font-size: 20px;
    line-height: 30px;
    letter-spacing: -0.625263px;
    color: #696871;
}

.feature1-group button {
    font-weight: bold;
    font-size: 17px;
    line-height: 20px;
    letter-spacing: -0.531474px;
    color: #5454D4;

    background: none;
    border: none;
    text-align: start;
}

.feature-mini-section {
    display: flex;
    margin: 10% 0%;
}

.feature-mini-group {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.feature-mini-group img {
    padding-left: 5%;
}

.feature-mini-head {
    font-size: 24px;
    line-height: 36px;
    text-align: center;
    letter-spacing: -0.750316px;
    width: 50%;
}

.feature-mini-text {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    letter-spacing: -0.500211px;
    color: #696871;
    width: 50%;
}

@media screen and (max-width: 1080px) {
    .feature1 {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .feature1 img {
        width: 70%;
    }

    .feature1-group {
        width: 90%;
        margin-bottom: 10%;
    }

    .feature-mini-section {
        flex-direction: column;
    }

}
.header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 30px;
}

.navbar a {
    text-decoration: none;
    font-weight: bold;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: -0.468947px;
    color: #696871;
    margin: 0px 30px 0px 30px;
}

.navbar .active {
    color: #000000;
}

.header button {
    padding: 15px 40px;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.531474px;
    color: #FFFFFF;

    background: #FF7143;
    border-radius: 10px;
    border: none;
}

@media screen and (max-width: 1080px) {
    .header {
        flex-direction: column;
    }
    .navbar {
        margin: 20px;
    }
}
.hero-section {
    display: flex;
    flex-direction: column;
    margin: 100px 0px 100px 0px;
}

.hero {
    display: flex;
    justify-content: space-between;
    margin: 0px 40px 70px 115px;
}

.group {
    display: flex;
    flex-direction: column;
    width: 459px;
    padding-top: 100px;
}

.group-head {
    font-size: 80px;
    line-height: 77px;
    letter-spacing: -2.50105px;
}

.group-desc {
    font-size: 20px;
    line-height: 30px;
    letter-spacing: -0.625263px;
    color: #696871;
    margin: 30px 0px 30px 0px;
}

.group button {
    width: 50%;
    padding: 20px;
    font-size: 17px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.531474px;
    color: #FFFFFF;

    background: #5454D4;
    border-radius: 10px;
    border: none;
}

.clients {
    display: flex;
    justify-content: space-around;
    padding: 0px 115px;
}

.clients img {
    width: 123.36px;
    height: 28.49px;
}

@media screen and (max-width: 1080px) {
    .hero {
        flex-direction: column;
        margin: 0% 10% 10% 10%;
    }

    .group {
        padding: 0% 10% 20% 0%;
        width: 90%;
    }

    .group-head {
        font-size: 50px;
        line-height: 47px;
    }

    .clients {
        flex-direction: column;
    }

    .clients img {
        margin-bottom: 10%;
    }
}
.pricing-section {
    display: flex;
    align-items: center;
    justify-content: center;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;

    background: #F8F8F8;
    border-radius: 10px;

    width: 25%;
    padding: 30px 0px 30px 0px;
    margin: 0% 0.5% 10% 0.5%;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.pricing-section > :hover,
.container:hover > p,
.container:hover > h4,
.container:hover > h4 > p {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: #FF7143;
    color: #FFFFFF;
}

.container:hover > button {
    color: #FFFFFF;
    background: #5454D4;
    border: none;
}

.container p {
    margin-bottom: 10%;
    font-size: 18px;
    line-height: 28px;
    text-align: center;
    letter-spacing: -0.562737px;
    color: #696871;
}

h4 {
    display: flex;
    margin-bottom: 10%;
    font-size: 60px;
    line-height: 56px;
    text-align: center;
    letter-spacing: -1.03448px;
    color: #1D293F;
}

.container button {
    background: #FFFFFF;
    border: 1px solid #EAE9F2;
    border-radius: 10px;
    padding: 0.8rem 6rem;

    font-size: 17px;
    line-height: 20px;
    text-align: center;
    letter-spacing: -0.531474px;
    color: #5454D4;
}

.pricing-banner {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 5%;
}

.pricing-banner > p {
    font-size: 60px;
    line-height: 70px;
    letter-spacing: -1.87579px;
    color: #19191B;
    width: 50%;
}

.pricing-banner button {
    background: #F7F7FB;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: -0.500211px;
    color: #19191B;
    padding: 10px 40px;
    margin-right: 1%;
}

.pricing-banner .active {
    color: #FFFFFF;
    background: #5454D4;
}

.pricing-banner > button:hover {
    color: #FFFFFF;
    background: #5454D4;
}

@media screen and (max-width: 1080px) {
    .pricing-banner {
        flex-direction: column;
        width: 70%;
        margin-left: 15%;
    }

    .pricing-banner > p {
        font-size: 35px;
        line-height: normal;
        margin-bottom: 10%;
    }

    .pricing-banner button {
        margin-bottom: 2%;
    }

    .pricing-section {
        flex-direction: column;
    }

    .container {
        width: 70%;
    }
}
